import { message } from 'antd'
import axios from 'axios'
import { useState } from 'react'
import { Row, Col, Button, Container, Card } from 'react-bootstrap'
import Form from 'react-bootstrap/Form'
import React from 'react'

const FindPatientRecord = () => {
  const [searchMobileNumber, setSearchMobileNumber] = useState([])
  const [name, setName] = useState('')
  const [uniqueId, setUniqueId] = useState('')
  const [age, setAge] = useState()
  const [mobileNumber, setMobileNumber] = useState('')
  const [bloodGroup, setBloodGroup] = useState('')
  const [address, setAddress] = useState('')
  const [gender, setGender] = useState('')
  const [zip, setZip] = useState('')
  const [visits, setVisits] = useState([])
  const [bills, setBills] = useState([])
  const [reports, setReports] = useState([])
  const [visible, setVisible] = useState(false)

  const getPatientData = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()
      const res = await axios.post('/api/v1/receptionist/getAllPatientData', { searchMobileNumber })
      if (res.data.success) {
        setVisible(true)
        setName(res.data.data.name)
        setUniqueId(res.data.data.uniqueId)
        setAge(res.data.data.age)
        setMobileNumber(res.data.data.mobileNumber)
        setBloodGroup(res.data.data.bloodGroup)
        setAddress(res.data.data.address)
        setGender(res.data.data.gender)
        setZip(res.data.data.zip)
        setVisits(res.data.data.visits)
        setBills(res.data.data.bills)
        setReports(res.data.data.reports)
        message.success('Patient Details Fetched Successfully')
      } else {
        message.error('something went wrong')
      }
    } catch (error) {
      message.error('Could not find the Patient')
      console.log(error)
    }
  }
  const formattedDate = (date) => {
    const dateObject = new Date(date)
    const format = `${dateObject.getFullYear()}-${String(dateObject.getMonth() + 1).padStart(
      2,
      '0',
    )}-${String(dateObject.getDate()).padStart(2, '0')} ${String(dateObject.getHours()).padStart(
      2,
      '0',
    )}:${String(dateObject.getMinutes()).padStart(2, '0')}:${String(
      dateObject.getSeconds(),
    ).padStart(2, '0')}`

    return format
  }

  return (
    <>
      <Row className="mb-3">
        <Form.Group
          as={Col}
          className="d-flex offset-md-7 col-md-5 col-sm-12 col-sm-12"
          controlId="formGridEmail"
        >
          <Form.Control
            type="text"
            onChange={(e) => setSearchMobileNumber(e.target.value)}
            value={searchMobileNumber}
            className="me-2"
            placeholder="Patient Mobile Number"
          />
          <Button onClick={getPatientData}>Search</Button>
        </Form.Group>
      </Row>

      <Container fluid className={`${visible ? 'd-block' : 'd-none'}`}>
        {/* Patient Personal Details */}
        <Row>
          <Container>
            <Card>
              <Card.Header>Patient Details</Card.Header>
              <Card.Body>
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formFirstName"
                  >
                    <Form.Label>Name</Form.Label>
                    <Form.Control type="text" placeholder="Enter Name" value={name} disabled />
                  </Form.Group>

                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formMobileNumber"
                  >
                    <Form.Label>Mobile Number</Form.Label>
                    <Form.Control
                      type="number"
                      placeholder="Enter Mobile Number"
                      value={mobileNumber}
                      disabled
                    />
                  </Form.Group>

                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formGender"
                  >
                    <Form.Label>Gender</Form.Label>
                    <Form.Control type="text" placeholder="Enter Age" value={gender} disabled />
                  </Form.Group>
                </Row>

                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formAge"
                  >
                    <Form.Label>Age</Form.Label>
                    <Form.Control type="text" placeholder="Enter Age" value={age} disabled />
                  </Form.Group>

                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formLastName"
                  >
                    <Form.Label>Unique Id</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Press Any Key"
                      className="disabled"
                      value={uniqueId}
                      disabled
                    />
                    {/* <Form.Control type="text" placeholder="Enter Last Name" value={email}  disabled /> */}
                  </Form.Group>

                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formBloodGroup"
                  >
                    <Form.Label>Blood Group</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Press Any Key"
                      className="disabled"
                      value={bloodGroup}
                      disabled
                    />
                  </Form.Group>
                </Row>

                <Form.Group className="mb-3" controlId="formGridAddress1">
                  <Form.Label>Address</Form.Label>
                  <Form.Control
                    placeholder="Enter Address Eg. 1234 Main St"
                    onChange={(e) => setAddress(e.target.value)}
                    value={address}
                    disabled
                  />
                </Form.Group>

                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    controlId="formGridZip"
                    className="col-4 col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                  >
                    <Form.Label>Zip</Form.Label>
                    <Form.Control
                      onChange={(e) => setZip(e.target.value)}
                      value={zip}
                      disabled
                      placeholder="Enter Zip"
                    />
                  </Form.Group>
                </Row>
              </Card.Body>
            </Card>
          </Container>
        </Row>
        {/* Patient last Two Visits */}
        <Row>
          <Container className="mt-4">
            <Card>
              <Card.Header>Patient Visits</Card.Header>
              <Card.Body>
                <Row className="mb-3">
                  <Container className="card border-0 bg-white">
                    {visits && visits.length > 0
                      ? visits.map((visit, idx) => {
                          return (
                            <>
                              <Container fluid>{formattedDate(visit.updatedAt)}</Container>

                              <Container fluid className="mt-2">
                                <Row className="mb-2">
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Checked By</Form.Label>
                                    <Form.Control
                                      type="text"
                                      value={visit.checkedBy}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Department</Form.Label>
                                    <Form.Control
                                      type="text"
                                      value={visit.department}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                </Row>
                                <Row className="mb-2">
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-12 col-xs-12 col-md-12"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Prescription</Form.Label>
                                    <Form.Control
                                      as="textarea"
                                      rows={3}
                                      value={visit.prescription}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                </Row>
                                <Row className="mb-2">
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Reports</Form.Label>
                                    <Form.Control
                                      type="text"
                                      value={visit.report}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                </Row>
                              </Container>
                            </>
                          )
                        })
                      : () => (
                          <Container fluid className="mt-2">
                            <Row className="mb-2">No Visits Found</Row>
                          </Container>
                        )}
                  </Container>
                </Row>
                <Row className="mb-3"></Row>
              </Card.Body>
            </Card>
          </Container>
        </Row>
        {/* Patient Last Two Bills */}
        <Row>
          <Container className="mt-4">
            <Card>
              <Card.Header>Patient Bills</Card.Header>
              <Card.Body>
                <Row className="mb-3">
                  <Container className="card border-0 bg-white">
                    {bills && bills.length > 0
                      ? bills.map((bill, idx) => {
                          return (
                            <>
                              <Container fluid>{formattedDate(bill.date)}</Container>
                              <Container fluid className="mt-2">
                                <Row className="mb-2">
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Department</Form.Label>
                                    <Form.Control
                                      type="text"
                                      value={bill.department}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                </Row>
                                <Row className="mb-2">
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Fee</Form.Label>
                                    <Form.Control type="text" value={bill.fee} required disabled />
                                  </Form.Group>
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Payment Mode</Form.Label>
                                    <Form.Control
                                      type="text"
                                      value={bill.paymentMode}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                </Row>
                              </Container>
                            </>
                          )
                        })
                      : () => (
                          <Container fluid className="mt-2">
                            <Row className="mb-2">No Bills Found</Row>
                          </Container>
                        )}
                  </Container>
                </Row>
                <Row className="mb-3"></Row>
              </Card.Body>
            </Card>
          </Container>
        </Row>
        {/* Patient Last Two Lab Reports */}
        <Row>
          <Container className="mt-4">
            <Card>
              <Card.Header>Lab Reports</Card.Header>
              <Card.Body>
                <Row className="mb-3">
                  <Container className="card border-0 bg-white">
                    <Container fluid className="mt-2">
                      <Row className="mb-2">
                        {reports && reports.length > 0
                          ? reports.map((report, idx) => {
                              return (
                                <>
                                  <Container fluid>Date : {formattedDate(report.createdAt)} </Container>
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Report Name</Form.Label>
                                    <Form.Control
                                      type="text"
                                      value={report.reportName}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                  <Form.Group
                                    as={Col}
                                    className="col-12 col-lg-6 col-xs-6 col-md-6"
                                    controlId="formFirstName"
                                  >
                                    <Form.Label>Requested By</Form.Label>
                                    <Form.Control
                                      type="text"
                                      value={report.requestedBy}
                                      required
                                      disabled
                                    />
                                  </Form.Group>
                                </>
                              )
                            })
                          : () => (
                              <Container fluid className="mt-2">
                                <Row className="mb-2">No Reports Found</Row>
                              </Container>
                            )}
                        {/* <Form.Group
                          as={Col}
                          className="col-12 col-lg-6 col-xs-6 col-md-6"
                          controlId="formFirstName"
                        >
                          <Form.Label>Report Name</Form.Label>
                          <Form.Control
                            type="text"
                            // value={service}
                            required
                            disabled
                          />
                        </Form.Group>
                        <Form.Group
                          as={Col}
                          className="col-12 col-lg-6 col-xs-6 col-md-6"
                          controlId="formFirstName"
                        >
                          <Form.Label>Requested By</Form.Label>
                          <Form.Control
                            type="text"
                            // value={checkedBy}
                            required
                            disabled
                          />
                        </Form.Group>
                      </Row>
                      <Row className="mb-2">
                        <Form.Group
                          as={Col}
                          className="col-12 col-lg-6 col-xs-6 col-md-6"
                          controlId="formFirstName"
                        >
                          <Form.Label>Fee</Form.Label>
                          <Form.Control
                            type="text"
                            // value={fee}
                            required
                            disabled
                          />
                        </Form.Group>
                        <Form.Group
                          as={Col}
                          className="col-12 col-lg-6 col-xs-6 col-md-6"
                          controlId="formFirstName"
                        >
                          <Form.Label>Payment Mode</Form.Label>
                          <Form.Control
                            type="text"
                            // value={paymentMode}
                            required
                            disabled
                          />
                        </Form.Group> */}
                      </Row>
                    </Container>
                  </Container>
                </Row>
                <Row className="mb-3"></Row>
              </Card.Body>
            </Card>
          </Container>
        </Row>
      </Container>
    </>
  )
}

export default FindPatientRecord
